.new {
	display: flex;
	width: 100%;
	.new-container {
		flex: 6;
		.top,
		.bottom {
			box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
			margin: 20px;
			padding: 10px;
			display: flex;
			h1 {
				font-size: 20px;
				color: lightgray;
			}
			.left {
				flex: 1;
				text-align: center;
				img {
					width: 100px;
					height: 100px;
					border-radius: 50%;
					object-fit: cover;
				}
			}
			.right {
				flex: 2;
				form {
					display: flex;
					flex-wrap: wrap;
					gap: 30px;
					justify-content: space-around;
					#file {
						display: none;
					}
					.form-input {
						width: 40%;
						label {
							display: flex;
							align-items: center;
							gap: 10px;
							.icon {
								cursor: pointer;
							}
						}
						input {
							width: 100%;
							padding: 5px;
							border: none;
							border-bottom: 1px solid grey;
						}
					}
					button {
						width: 150px;
						padding: 10px;
						border: none;
						background-color: teal;
						color: white;
						font-weight: bold;
						margin-top: 10px;
						cursor: pointer;
					}
				}
			}
		}
	}
}
